<template>
  <v-container
    id="regular-tables-view"
    fluid
    tag="section"
  >
    <material-card
      icon="mdi-clipboard-text"
      icon-small
      color="primary"
      title="Ingresos"
    >
      <v-data-table
        :headers="headers"
        :items="lista_ingreso"
        sort-by=""
        class="elevation-1"
        :loading="cargando"
        loading-text="Cargando... Espere por favor"
        no-data-text="No existen datos"
        no-results-text="No se encontraron registros coincidentes"
        :search="search"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          'items-per-page-text':'Items por página'
        }"
      >
        <template v-slot:top>
          <v-toolbar
            flat
          >
          <v-row>
            <v-col
              cols="12"
              sm="12"
              md="12"
            >
              <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Búsqueda"
              single-line
              hide-details
              />
            </v-col>
          </v-row>
            <v-spacer />
          <v-row>
            <v-col
              cols="4"
              sm="4"
              md=""
            >
              <v-dialog
                v-model="dialogReporte"
                max-width="500px"
              >
                <template v-slot:activator="{ on, attrs }" >
                  <v-btn
                    color="primary darken-3"
                    dark
                    class="mb-2"
                    v-bind="attrs"
                    v-on="on"
                    
                  >
                    Reporte Lista Ingresos
                  </v-btn>
                </template>
                <v-card>
                  <v-card-title>
                    <span class="text-h5">Generar Reporte de Ingresos</span>
                  </v-card-title>
                  <v-card-text>
                    <v-form ref="form" v-model="valid">
                      <v-container>
                        <!-- {{reporte}} -->
                        <v-row>
                          <v-col
                            cols="12"
                            sm="12"
                            md="12"
                          >
                            <v-menu
                              ref="menu"
                              v-model="menu"
                              :close-on-content-click="false"
                              :nudge-right="40"
                              transition="scale-transition"
                              offset-y
                              min-width="auto"
                            >
                              <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                  v-model="dateRangeText"
                                  label="Seleccione el rango de fechas del reporte"
                                  prepend-icon="mdi-calendar"
                                  readonly
                                  v-bind="attrs"
                                  v-on="on"
                                ></v-text-field>
                              </template>
                              <v-date-picker
                                v-model="dates"
                                @input="menu = true"
                                range
                                :first-day-of-week="1"
                                locale="es"
                                :rules="fieldRules"
                              >
                              </v-date-picker>
                            </v-menu>
                          </v-col>
                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                          >
                            <v-autocomplete
                              v-model="reporte.departamento"
                              :items="lista_distrito"
                              item-text="descripcion"
                              item-value="id"
                              label="Departamento"
                              hint="Seleccione el departamento"
                              persistent-hint
                              no-data-text="No existen datos"
                            >
                            </v-autocomplete>
                          </v-col>
                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                          >
                            <v-autocomplete
                              v-model="reporte.tipoIngreso"
                              :items="lista_tipoingreso"
                              item-text="descripcion"
                              item-value="id"
                              label="Tipo de Ingreso"
                              hint="Seleccione el tipo"
                              persistent-hint
                              no-data-text="No existen datos"
                            >
                            </v-autocomplete>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-form>
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer />
                    <v-btn
                      text
                      @click="closeReporte"
                    >
                      Cancelar
                    </v-btn>
                    <v-btn
                      :loading="estadoBoton"
                      text
                      @click="generarReporte"
                      :disabled="!valid"
                    >
                      Generar
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-col>
            <v-spacer />
            <v-col
              cols="4"
              sm="4"
              md=""
            >
              <v-dialog
                v-model="dialogReporteCantidad"
                max-width="500px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    color="primary darken-3"
                    dark
                    class="mb-2"
                    v-bind="attrs"
                    v-on="on"
                    
                  >
                    Reporte Cantidad Ingresos
                  </v-btn>
                </template>
                <v-card>
                  <v-card-title>
                    <span class="text-h5">Generar Reporte de Ingresos</span>
                  </v-card-title>
                  <v-card-text>
                    <v-form ref="form" v-model="valid">
                      <v-container>
                        {{reporte}}
                        <v-row>
                          <v-col
                            cols="12"
                            sm="12"
                            md="12"
                          >
                            <v-menu
                              ref="menu2"
                              v-model="menu2"
                              :close-on-content-click="false"
                              :nudge-right="40"
                              transition="scale-transition"
                              offset-y
                              min-width="auto"
                            >
                              <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                  v-model="dateRangeText"
                                  label="Seleccione el rango de fechas del reporte"
                                  prepend-icon="mdi-calendar"
                                  readonly
                                  v-bind="attrs"
                                  v-on="on"
                                ></v-text-field>
                              </template>
                              <v-date-picker
                                v-model="dates"
                                @input="menu2 = true"
                                range
                                :first-day-of-week="1"
                                locale="es"
                                :rules="fieldRules"
                              >
                              </v-date-picker>
                            </v-menu>
                          </v-col>
                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                          >
                            <v-autocomplete
                              v-model="reporte.departamento"
                              :items="lista_distrito"
                              item-text="descripcion"
                              item-value="id"
                              label="Departamento"
                              hint="Seleccione el departamento"
                              persistent-hint
                              no-data-text="No existen datos"
                            >
                            </v-autocomplete>
                          </v-col>
                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                          >
                            <v-autocomplete
                              v-model="reporte.tipoIngreso"
                              :items="lista_tipoingreso"
                              item-text="descripcion"
                              item-value="id"
                              label="Tipo de Ingreso"
                              hint="Seleccione el tipo"
                              persistent-hint
                              no-data-text="No existen datos"
                            >
                            </v-autocomplete>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-form>
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer />
                    <v-btn
                      text
                      @click="closeReporteCantidad"
                    >
                      Cancelar
                    </v-btn>
                    <v-btn
                      :loading="estadoBoton"
                      text
                      @click="generarReporteCantidad"
                      :disabled="!valid"
                    >
                      Generar
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-col>
            

            <v-spacer />
            <!-- <template v-slot:activator="{ on, attrs }"> -->
            <v-col
              cols="4"
              sm="4"
              md=""
            >
              <v-btn
                color="primary"
                dark
                class="mb-2"
                to="/tables/ingreso/form"
                
              >
                Nuevo Registro
              </v-btn>
            </v-col>
            
              <!-- </template> -->
            
            <v-dialog
              v-model="dialog"
              max-width="500px"
            >
              
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitulo }}</span>
                </v-card-title>
                <v-card-text>
                  <v-form ref="form" v-model="valid">
                    <v-container>
                      <v-row>
                        <v-col
                          cols="12"
                          sm="12"
                          md="12"
                        >
                          <v-text-field
                            v-model="ingreso.descripcion"
                            label="Descripción"
                            :counter="50"
                            :rules="fieldRules"
                            :autofocus="true"
                            hint="Introduzca la descripción"
                            persistent-hint
                          />
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-form>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    text
                    @click="close"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    :loading="estadoBoton"
                    text
                    @click="guardar"
                    :disabled="!valid"
                  >
                    Guardar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>


            <v-dialog
              v-model="dialogDelete"
              max-width="500px"
            >
              <v-card>
                <v-card-title class="text-h5">
                  Está seguro de eliminar el registro?
                </v-card-title>
                <v-spacer />
                <v-card-subtitle class="text-h6">
                  {{ ingreso.descripcion }}
                </v-card-subtitle>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    color="blue darken-1"
                    text
                    @click="closeEliminar"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    color="red darken-1"
                    text
                    @click="eliminarConfirm"
                  >
                    Eliminar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-row>
            
          </v-toolbar>
        </template>
        <template v-slot:[`item.acciones`]="{ item }">
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="yellow darken-3"
            @click="editar(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-pencil
            </v-icon>
          </v-btn>
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="red darken-2"
            @click="eliminar(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-delete
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </material-card>
    <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
      :color="color"
    >
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color=""
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
  import * as ingresoService from "../services/ingresoService"
  import * as tipoIngresoService from "../services/tipoIngresoService"
  import * as distritoService from "../services/distritoService"
  import jsPDF from 'jspdf'
  import autoTable from 'jspdf-autotable'

  export default {
    data () {
      return {
        estadoBoton: false,
        dialog: false,
        dialogDelete: false,
        dialogReporte: false,
        dialogReporteCantidad: false,
        lista_ingreso: [],
        lista_tipoingreso: [],
        lista_distrito: [],
        cargando: true,
        ingreso: {

        },
        reporte: {
          // fechaInicio: null,
          // fechaFin: Date.now(),
          tipoIngreso: 0,
          departamento: 0
        },
        search: '',
        headers: [
          {
            text: 'ID',
            align: 'start',
            sortable: false,
            value: 'id',
          },
          { text: 'Departamento', value: 'Distrito.descripcion' },
          { text: 'Fecha', value: 'fecha' },
          { text: 'Depósito', value: 'deposito' },
          { text: 'Detalle', value: 'detalle' },
          { text: 'Monto Total', value: 'total' },
          { text: 'Acciones', value: 'acciones', sortable: false, align: 'center' },
        ],
        editedIndex: -1,
        defaultIngreso: {

        },
        defaultReporte: {
          tipoIngreso: 0,
          departamento: 0
        },
        color: '',
        snackbar: false,
        text: '',
        timeout: 3000,
        valid: true,
        fieldRules: [
          v => !!v || 'El campo es requerido',
          v => (v && v.length <= 50) || 'El campo debe ser menor a 50 caracteres',
        ],
        menu: false,
        menu2: false,
        dates: []
      }
    },
    computed: {
      formTitulo () {
        return this.editedIndex === -1 ? 'Nuevo Registro' : 'Editar Registro'
      },
      dateRangeText () {
        this.reporte.fechaInicio = this.dates[0]
        this.reporte.fechaFin = this.dates[1]
        return this.dates.join(' ~ ')
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeEliminar()
      },
      dialogReporte (val) {
        val || this.closeReporte()
      },
      dialogReporteCantidad (val) {
        val || this.closeReporteCantidad()
      },
    },
    async mounted () {
      this.obtener()
      this.obtenerTipoIngreso()
      this.obtenerDistrito()
    },
    methods: {
      async obtener () {
        const res = await ingresoService.listar()
        // console.log(datos)
        this.lista_ingreso = res.data
        this.cargando = false
      },
      async obtenerTipoIngreso () {
        const res = await tipoIngresoService.listar()
        // console.log(res)
        this.lista_tipoingreso = res.data
        this.cargando = false
      },
      async obtenerDistrito () {
        const res = await distritoService.listar()
        // console.log(res)
        this.lista_distrito = res.data
        this.cargando = false
      },
      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.ingreso = Object.assign({}, this.defaultIngreso)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      closeReporte () {
        this.dates = []
        this.dialogReporte = false
        this.$nextTick(() => {
          this.reporte = Object.assign({}, this.defaultReporte)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      closeReporteCantidad () {
        this.dates = []
        this.dialogReporteCantidad = false
        this.$nextTick(() => {
          this.reporte = Object.assign({}, this.defaultReporte)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      async guardar () {
        let res = null
        this.validate();
        this.estadoBoton = true
        if (this.editedIndex > -1) {
          const mar = this.lista_ingreso[this.editedIndex]
          res = await ingresoService.modificar(this.ingreso, mar.id)
          Object.assign(this.lista_ingreso[this.editedIndex], this.ingreso)
        } else {
          res = await ingresoService.guardar(this.ingreso)
          // console.log(res);
          this.lista_ingreso.push(this.ingreso)
        }
        if(res.status == 200){
          this.text = "Registro guardado"
          this.snackbar = true
          this.color = "green"
        } else {
          this.text = res.data.message
          this.snackbar = true
          this.color = "dark-red"
        }
        this.obtener()
        this.estadoBoton = false
        this.close()
      },
      editar (item) {
        this.editedIndex = this.lista_ingreso.indexOf(item)
        this.ingreso = Object.assign({}, item)
        this.dialog = true
      },
      eliminar (item) {
        this.editedIndex = this.lista_ingreso.indexOf(item)
        this.ingreso = Object.assign({}, item)
        this.dialogDelete = true
      },
      async eliminarConfirm () {
        try {
          const mar = this.lista_ingreso[this.editedIndex]
          const res = await ingresoService.eliminar(mar.id)
          if(res.status == 200){
            this.text = res.data.message
            this.snackbar = true
            this.color = "green"
            this.lista_ingreso.splice(this.editedIndex, 1)
          } else {
            this.text = res.data.message
            this.snackbar = true
            this.color = "dark-red"
          }
          // if (!res.error) {
          //   this.lista_ingreso.splice(this.editedIndex, 1)
          // }
          this.closeEliminar()
        } catch (error) {
          console.log(error);
        }
      },
      closeEliminar () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.ingreso = Object.assign({}, this.defaultIngreso)
          this.editedIndex = -1
        })
      },
      validate() {
        this.$refs.form.validate()
      },
      
      async generarReporte(){
        const res = await ingresoService.reporteIngresos(this.reporte.fechaInicio, this.reporte.fechaFin, this.reporte.tipoIngreso, this.reporte.departamento)
        let data = res.data
        this.cargando = false
        let info = []
        let aux = null
        let contador = 0
        data.forEach((element, index, array) => {
          if (aux == element.id) {
            info.push(['', '', '', '', element.tipoIngresoDescripcion, element.cantidad, element.precioUnitario, element.cantidad*element.precioUnitario, '^^^'])
          } else {
            contador = contador + 1
            info.push([contador, element.distritoDescripcion, element.fecha, element.deposito, element.tipoIngresoDescripcion, element.cantidad, element.precioUnitario, element.cantidad*element.precioUnitario, element.total ])
          }
          aux = element.id
        });

        let doc = new jsPDF()


        var image1 = new Image();
        // var imgData = 'data:image/jpeg;base64,'+ Base64.encode('assets/login.jpg');
        image1.src = 'data:image/jpeg;base64,'; /// URL de la imagen
        doc.addImage(image1, 'JPEG', 20, 10, 15, 20); // Agregar la imagen al PDF (X, Y, Width, Height)

        let y = 20
        doc.setFontSize(10);
        doc.text(62, y, `COLEGIO DE BIOQUÍMICA Y FARMACIA DE BOLIVIA`);
        doc.setFontSize(14);
        doc.text(20, y = y + 20, `Reporte de Ingresos`);
        doc.setFontSize(12);
        doc.text(20, y = y + 7, `Periodo del ${this.reporte.fechaInicio} al ${this.reporte.fechaFin}`);


        doc.autoTable({
          head: [['N°', 'Departamento', 'Fecha', 'Depósito', 'Detalle', 'Cantidad', 'Precio U.', 'SubTotal', 'Total']],
          body: info,
          margin: {top:50}
        })
        window.open(doc.output('bloburl'))
        this.closeReporte()
      },

      async generarReporteCantidad(){
        const res = await ingresoService.reporteIngresosCantidad(this.reporte.fechaInicio, this.reporte.fechaFin, this.reporte.tipoIngreso, this.reporte.departamento)
        let data = res.data
        this.cargando = false

        let doc = new jsPDF()

        var image1 = new Image();
        // var imgData = 'data:image/jpeg;base64,'+ Base64.encode('assets/login.jpg');
        image1.src = 'data:image/jpeg;base64,'; /// URL de la imagen
        doc.addImage(image1, 'JPEG', 20, 10, 15, 20); // Agregar la imagen al PDF (X, Y, Width, Height)

        let y = 20
        doc.setFontSize(10);
        doc.text(62, y, `COLEGIO DE BIOQUÍMICA Y FARMACIA DE BOLIVIA`);
        doc.setFontSize(14);
        doc.text(20, y = y + 20, `Reporte de Cantidad y Monto de Ingresos`);
        doc.setFontSize(12);
        doc.text(20, y = y + 7, `Periodo del ${this.reporte.fechaInicio} al ${this.reporte.fechaFin}`);
        doc.setFontSize(11);
        if (this.reporte.departamento != 0) {
          const dep = await distritoService.listarId(this.reporte.departamento)
          let departamento = dep.data
          doc.text(20, y = y + 7, `Departamento: ${departamento.descripcion}`);
        } else {
          doc.text(20, y = y + 7, `Departamento: Nivel Nacional`);
        }
        // doc.setFont("italic");
        doc.text(20, y = y + 10, `\u2022 Cantidad de Depositos: ${data.cantidadDepositos}`);
        doc.text(20, y = y + 7, `\u2022 Monto de Depositos: ${data.montoDepositos} Bs.`);
        // doc.setFont("italic")
        if (data.cantidadInscripciones != 0) {
          doc.text(20, y = y + 10, `\u2022 Cantidad de Inscripciones: ${data.cantidadInscripciones}`);
          doc.text(20, y = y + 7, `\u2022 Monto de Inscripciones: ${data.montoInscripciones} Bs.`);
        }
        if (data.cantidadRecarnetizaciones != 0) {
          doc.text(20, y = y + 10, `\u2022 Cantidad de Recarnetizaciones: ${data.cantidadRecarnetizaciones}`);
          doc.text(20, y = y + 7, `\u2022 Monto de Recarnetizaciones: ${data.montoRecarnetizaciones} Bs.`);
        }
        if (data.cantidadMarbetes != 0) {
          doc.text(20, y = y + 10, `\u2022 Cantidad de Marbetes: ${data.cantidadMarbetes}`);
          doc.text(20, y = y + 7, `\u2022 Monto de Marbetes: ${data.montoMarbetes} Bs.`);
        }
        if (data.cantidadPosgrado != 0) {
          doc.text(20, y = y + 10, `\u2022 Cantidad de Cert. de Posgrado: ${data.cantidadPosgrado}`);
          doc.text(20, y = y + 7, `\u2022 Monto de Cert. de Posgrado: ${data.montoPosgrado} Bs.`);
        }
        if (data.cantidadCuotas != 0) {
          doc.text(20, y = y + 10, `\u2022 Cantidad de Cuotas: ${data.cantidadCuotas}`);
          doc.text(20, y = y + 7, `\u2022 Monto de Cuotas: ${data.montoCuotas} Bs.`);
        }
        if (data.cantidadCuotasScz != 0) {
          doc.text(20, y = y + 10, `\u2022 Cantidad de Cuotas (SCZ): ${data.cantidadCuotasScz}`);
          doc.text(20, y = y + 7, `\u2022 Monto de Cuotas (SCZ): ${data.montoCuotasScz} Bs.`);
        }
        if (data.cantidadLegalizaciones != 0) {
          doc.text(20, y = y + 10, `\u2022 Cantidad de Legalizaciones: ${data.cantidadLegalizaciones}`);
          doc.text(20, y = y + 7, `\u2022 Monto de Legalizaciones: ${data.montoLegalizaciones} Bs.`);
        }
        
        // doc.autoTable({
        //   head: [['N°', 'Departamento', 'Fecha', 'Depósito', 'Detalle', 'Cantidad', 'Precio U.', 'SubTotal', 'Total']],
        //   body: info
        // })
        window.open(doc.output('bloburl'))
        this.closeReporteCantidad()
      }
    },
  }
</script>
